.container_header{
    position:relative;
   
}

.container_header_image{
    width: 100vw;
    height:100vh;
    object-fit: cover;
    opacity: 0.4;
}

.header_paragraphe{
    width: 80%;
    position:absolute;
   bottom:35%;
    left:0; 
   padding-left:39px;
}

h1 {
    font-size:50px;
    font-family: "Lato", sans-serif;
    margin-bottom:0;
}
p{  
    font-family: "Montserrat", sans-serif;
    font-size: 14px;
    font-weight: 600;
}



.icons_reseaux{
    display:flex;
    gap:20px;
}
.icons{
    width: 20px;
    cursor:pointer;
}
@media screen and (min-width: 768px){

    .header_paragraphe p{
        font-size: 20px;
    }


    .icons{
        width: 30px;
    }
}

@media screen and (min-width:1024px){


    .header_paragraphe{
        padding: 50px;
        border-radius: 10%;
        bottom:30%;
    }

    .container_header_image{
        width: 100%;
        height:100vh;
    }

    h1{
        font-size:74px;

    }

    p{
        width: 32%;
        font-size:24px;
        margin-top:50px;
        font-weight: 500;
    }

    .icons{
        width: 35px;
        padding-right:20px;
    }
}